.mdui-typo {
  line-height: 1.8;
  word-wrap: break-word;
  /* a */
  /* small */
  /* blockquote */
  /* mark */
  /* h1 - h6 */
  /* code */
  /* pre code */
  /* abbr */
  /* ins */
  /* u */
  /* del */
  /* hr */
  /* pre */
  /* kbd */
  /* ul / ol */
  /* img, video */
}
.mdui-typo address,
.mdui-typo caption,
.mdui-typo cite,
.mdui-typo code,
.mdui-typo dfn,
.mdui-typo th {
  font-weight: 400;
