}
.mdui-typo q::before,
.mdui-typo q::after {
  content: '';
}
.mdui-typo pre,
.mdui-typo code,
.mdui-typo kbd,
.mdui-typo samp,
.mdui-typo pre tt {
  font-family: Consolas, Courier, 'Courier New', monospace;
}
.mdui-typo figcaption {
  color: rgba(0, 0, 0, 0.54);
  font-size: 80%;
}
.mdui-typo [draggable],
.mdui-typo [draggable="true"] {
  cursor: move;
}
.mdui-typo [draggable="false"] {
  cursor: inherit;
}
.mdui-typo p,
.mdui-typo pre,
.mdui-typo ul,
.mdui-typo ol,
.mdui-typo dl,
.mdui-typo form,
.mdui-typo hr,
.mdui-typo figure,
.mdui-typo table,
