.mdui-typo a {
  color: #FF4081;
  position: relative;
  display: inline-block;
  overflow: hidden;
  text-decoration: none;
  vertical-align: top;
  outline: none;
}
.mdui-typo a::before {
  position: absolute;
  top: auto;
  bottom: 1px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #FF4081;
  transform: scaleX(0);
  backface-visibility: hidden;
  transition: all 0.2s;
  content: ' ';
}
.mdui-typo a:hover::before,
.mdui-typo a:focus::before {
  transform: scaleX(1);
}
