  cursor: pointer;
}
.mdui-textfield-input[rows] {
  height: auto !important;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
/* 文本框 label */
.mdui-textfield-label {
  display: block;
  width: 100%;
  color: rgba(0, 0, 0, 0.54);
  font-size: 16px;
  transform: scale(0.75) translateY(0);
  transform-origin: left;
  transition: all 0.2s;
  pointer-events: none;
}
/* 表单验证错误提示、帮助文本提示 */
.mdui-textfield-error,
.mdui-textfield-helper {
  position: absolute;
  bottom: 8px;
  height: 12px;
  font-size: 12px;
  line-height: 12px;
  overflow: hidden;
  white-space: nowrap;
