  /* 覆盖在媒体元素上的标题 */
}
.mdui-card-media-covered .mdui-card-primary-title {
  opacity: 1;
}
.mdui-card-media-covered .mdui-card-primary-subtitle {
  opacity: 0.7;
}
/* 覆盖在媒体元素顶部 */
.mdui-card-media-covered-top {
  top: 0;
  bottom: auto;
}
/* 覆盖层透明 */
.mdui-card-media-covered-transparent {
  background: transparent;
}
/* 覆盖层渐变 */
.mdui-card-media-covered-gradient {
  background: linear-gradient(to top, rgba(0, 0, 0, 0.26), rgba(0, 0, 0, 0));
}
.mdui-card-media-covered-gradient.mdui-card-media-covered-top {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.26), rgba(0, 0, 0, 0));
}
/**
 * =============================================================================
 * ************   Card dark   ************
 * =============================================================================
 */
.mdui-theme-layout-dark .mdui-card {
  color: #fff;
  background-color: #424242;
}
/**
 * =============================================================================
 * ************   Tab   ************
 * =============================================================================
 *
 * 在手机上选项卡始终平分或可滚动，在平板以上的设备上默认左对齐，可以选择居中对齐，或全宽等分
 */
/* 选项卡，默认的选项卡为全宽 */
.mdui-tab {
  position: relative;
  display: flex;
  min-height: 48px;
  max-height: 72px;
  margin: 0 auto;
  padding: 0;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}
.mdui-tab a {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  min-width: 72px;
  min-height: 48px;
  max-height: 72px;
  padding: 12px;
  overflow: hidden;
  color: inherit;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  text-overflow: ellipsis;
  outline: none;
  cursor: pointer;
  opacity: 0.7;
  user-select: none;
}
.mdui-tab a .mdui-icon {
  opacity: 0.7;
}
@media (min-width: 600px) {
  .mdui-tab a {
    flex: none;
    min-width: 99px;
    max-width: 264px;
    padding: 12px 24px;
  }
}
@media (min-width: 1024px) {
  .mdui-tab a {
    min-width: 112px;
  }
}
@media (min-width: 1440px) {
  .mdui-tab a {
    min-width: 136px;
  }
}
@media (min-width: 1920px) {
  .mdui-tab a {
    min-width: 160px;
  }
}
.mdui-tab a label {
  display: block;
  width: 100%;
  cursor: pointer;
}
.mdui-tab a .mdui-icon + label {
  margin-top: 8px;
}
.mdui-tab a[disabled] {
  cursor: default;
  opacity: 0.38;
}
.mdui-tab a[disabled] label {
  cursor: default;
}
.mdui-tab .mdui-tab-active {
  color: #3F51B5;
  opacity: 1;
}
.mdui-tab .mdui-tab-active .mdui-icon {
  opacity: 1;
}
/* 选项卡居中 */
@media (min-width: 600px) {
  .mdui-tab-centered::before {
    flex-grow: 1;
    content: ' ';
  }
  .mdui-tab-centered::after {
    flex-grow: 1;
    content: ' ';
  }
  .mdui-tab-centered a {
    flex: none;
  }
}
/* 选项卡始终全宽等分 */
.mdui-tab-full-width a {
  flex: 1;
  max-width: none;
}
/* 可横向滚动的选项卡 */
.mdui-tab-scrollable {
  padding-left: 56px;
}
.mdui-tab-scrollable a {
  flex: none;
}
@media (max-width: 599px) {
  .mdui-tab-scrollable {
    padding-left: 60px;
  }
}
/* 下划线指示器 */
.mdui-tab-indicator {
  position: absolute;
  bottom: 0;
  height: 2px;
  background-color: #3F51B5;
  transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: left, width;
}
/**
 * =============================================================================
 * ************   Tab 主色   ************
 * =============================================================================
 */
/* 带背景色的选项卡 */
.mdui-tab[class*="mdui-color-"]:not(.mdui-color-white) .mdui-tab-active {
  color: inherit;
}
.mdui-tab[class*="mdui-color-"]:not(.mdui-color-white) .mdui-tab-indicator {
  background-color: #FFFFFF;
}
.mdui-tab[class*="mdui-color-"]:not(.mdui-color-white)::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background: transparent;
}
@media (min-width: 1024px) {
  .mdui-tab[class*="mdui-color-"]:not(.mdui-color-white)::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
}
.mdui-tab[class*="mdui-color-"]:not(.mdui-color-white)::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
}
/**
 * =============================================================================
 * ************   Subheader 副标题   ************
 * =============================================================================
 *
 * 用于 List 和 Grid list 组件
 */
.mdui-subheader,
.mdui-subheader-inset {
  position: relative;
  box-sizing: border-box;
  height: 48px;
  padding-right: 16px;
  padding-left: 16px;
  color: rgba(0, 0, 0, 0.54);
  font-weight: 500;
  font-size: 14px;
  line-height: 48px;
  cursor: default;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.mdui-subheader-inset {
  padding-left: 72px;
}
/**
 * =============================================================================
 * ************   Subheader dark   ************
 * =============================================================================
 */
.mdui-theme-layout-dark .mdui-subheader,
.mdui-theme-layout-dark .mdui-subheader-inset {
  color: rgba(255, 255, 255, 0.7);
}
/**
 * =============================================================================
 * ************   Grid List 网格列表   ************
 * =============================================================================
 */
/* 调整网格边距 */
.mdui-grid-list {
  margin: 0 -2px;
}
.mdui-grid-list .mdui-col,
.mdui-grid-list [class*="mdui-col-xs-"],
.mdui-grid-list [class*="mdui-col-sm-"],
.mdui-grid-list [class*="mdui-col-md-"],
.mdui-grid-list [class*="mdui-col-lg-"],
.mdui-grid-list [class*="mdui-col-xl-"] {
  padding-right: 2px;
  padding-left: 2px;
}
/* 单元格 */
.mdui-grid-tile {
  position: relative;
  box-sizing: border-box;
  margin-bottom: 4px;
  overflow: hidden;
}
.mdui-grid-tile img {
  display: block;
  width: 100%;
}
/* 操作栏 */
.mdui-grid-tile-actions {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  min-height: 48px;
  max-height: 68px;
  padding: 16px;
  color: #fff;
  background: rgba(0, 0, 0, 0.2);
}
.mdui-grid-tile-actions .mdui-icon {
  color: #fff;
}
/* 操作栏内的文本 */
.mdui-grid-tile-text {
  flex: 1;
  overflow: hidden;
}
/* 标题 */
.mdui-grid-tile-title {
  height: 16px;
  font-size: 16px;
  line-height: 16px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.mdui-grid-tile-title .mdui-icon {
  margin-right: 8px;
}
/* 副标题 */
.mdui-grid-tile-subtitle {
  height: 18px;
  margin-top: 4px;
  font-size: 12px;
  line-height: 18px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.mdui-grid-tile-subtitle .mdui-icon {
  margin-right: 8px;
  font-size: 18px;
}
/* 操作栏内的按钮 */
.mdui-grid-tile-buttons {
  flex: none;
  margin: -8px;
  white-space: nowrap;
}
.mdui-grid-tile-buttons .mdui-btn {
  margin-left: 8px;
}
.mdui-grid-tile-buttons .mdui-btn:first-child {
  margin-left: 0;
}
.mdui-grid-tile-text + .mdui-grid-tile-buttons {
  margin-left: 8px;
}
.mdui-grid-tile-buttons + .mdui-grid-tile-text {
  margin-left: 16px;
}
/* 操作栏位于顶部 */
.mdui-grid-tile-actions-top {
  top: 0;
  bottom: auto;
}
/* 操作栏背景透明 */
.mdui-grid-tile-actions-transparent {
  background: transparent;
}
/* 操作栏背景渐变 */
.mdui-grid-tile-actions-gradient {
  background: linear-gradient(to top, rgba(0, 0, 0, 0.26), rgba(0, 0, 0, 0));
}
.mdui-grid-tile-actions-gradient.mdui-grid-tile-actions-top {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.26), rgba(0, 0, 0, 0));
}
/**
 * =============================================================================
 * ************   List 列表   ************
 * =============================================================================
 */
/* 列表 */
.mdui-list {
  margin: 0;
  padding: 8px 0;
  list-style: none;
  background-color: transparent;
}
.mdui-list .mdui-list {
  padding: 0;
}
.mdui-list > .mdui-divider,
.mdui-list > .mdui-divider-light,
.mdui-list > .mdui-divider-dark,
.mdui-list > .mdui-divider-inset,
.mdui-list > .mdui-divider-inset-light,
.mdui-list > .mdui-divider-inset-dark {
  margin-top: 8px;
  margin-bottom: 8px;
}
.mdui-list a {
  color: inherit;
  text-decoration: none;
}
.mdui-list .mdui-subheader,
.mdui-list .mdui-subheader-inset {
  margin-top: 8px;
}
.mdui-list .mdui-subheader::before,
.mdui-list .mdui-subheader-inset::before {
  position: absolute;
  right: 0;
  left: 0;
  display: block;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.12);
  content: ' ';
}
.mdui-list .mdui-subheader:first-child,
.mdui-list .mdui-subheader-inset:first-child {
  margin-top: -8px;
}
.mdui-list .mdui-subheader:first-child::before,
.mdui-list .mdui-subheader-inset:first-child::before {
  background-color: transparent;
}
.mdui-list .mdui-subheader-inset::before {
  left: 72px;
}
/* 列表项 */
.mdui-list-item {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  min-height: 48px;
  padding: 0 16px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.mdui-list-item:hover {
  background-color: rgba(0, 0, 0, 0.08);
}
.mdui-list-item::after {
  height: 48px;
  visibility: hidden;
  content: ' ';
}
/* 列表项图标 */
.mdui-list-item-icon {
  width: 24px;
  min-width: 24px;
  height: 24px;
  color: rgba(0, 0, 0, 0.54);
}
/* 列表项头像 */
.mdui-list-item-avatar {
  min-width: 40px;
  max-width: 40px;
  height: 40px;
  margin-top: 8px;
  margin-bottom: 8px;
  color: #FFFFFF;
  line-height: 40px;
  text-align: center;
  background-color: #BDBDBD;
  border-radius: 50%;
}
.mdui-list-item-avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
/* 列表项内容 */
.mdui-list-item-content {
  flex-grow: 1;
  padding-top: 14px;
  padding-bottom: 14px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
}
/* 列表项内容的副内容 */
.mdui-list-item-text {
  font-size: 14px;
  opacity: 0.54;
}
.mdui-list-item-title ~ .mdui-list-item-text {
  margin-top: 4px;
}
/* 激活状态的列表项 */
.mdui-list-item-active {
  font-weight: 700;
  background-color: rgba(0, 0, 0, 0.08);
}
.mdui-list-item-active .mdui-list-item-content {
  font-weight: 700;
}
.mdui-list-item-active .mdui-list-item-text {
  font-weight: 400;
}
/* 限制文本高度 */
.mdui-list-item-one-line,
.mdui-list-item-two-line,
.mdui-list-item-three-line {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
}
.mdui-list-item-one-line {
  -webkit-line-clamp: 1;
  height: 20px;
}
.mdui-list-item-two-line {
  -webkit-line-clamp: 2;
  height: 40px;
}
.mdui-list-item-three-line {
  -webkit-line-clamp: 3;
  height: 60px;
}
/* 列表项内的元素间添加间距 */
.mdui-list-item-icon ~ .mdui-list-item-content {
  margin-left: 32px;
}
.mdui-checkbox ~ .mdui-list-item-content,
.mdui-radio ~ .mdui-list-item-content,
.mdui-switch ~ .mdui-list-item-content {
  margin-left: 20px;
}
.mdui-list-item-avatar ~ .mdui-list-item-content {
  margin-left: 16px;
}
.mdui-list-item-content ~ .mdui-list-item-icon,
.mdui-list-item-content ~ .mdui-list-item-avatar,
.mdui-list-item-content ~ .mdui-checkbox,
.mdui-list-item-content ~ .mdui-radio,
.mdui-list-item-content ~ .mdui-switch {
  margin-left: 16px;
}
.mdui-list-item-content ~ .mdui-checkbox,
.mdui-list-item-content ~ .mdui-radio {
  padding-left: 24px;
}
/* 密集型列表 */
.mdui-list-dense {
  padding: 4px 0;
  font-size: 13px;
}
.mdui-list-dense > .mdui-divider,
.mdui-list-dense > .mdui-divider-light,
.mdui-list-dense > .mdui-divider-dark,
.mdui-list-dense > .mdui-divider-inset,
.mdui-list-dense > .mdui-divider-inset-light,
.mdui-list-dense > .mdui-divider-inset-dark {
  margin-top: 4px;
  margin-bottom: 4px;
}
.mdui-list-dense .mdui-subheader,
.mdui-list-dense .mdui-subheader-inset {
  height: 40px;
  margin-top: 4px;
  font-size: 12px;
  line-height: 40px;
}
.mdui-list-dense .mdui-subheader:first-child,
.mdui-list-dense .mdui-subheader-inset:first-child {
  margin-top: -4px;
}
.mdui-list-dense .mdui-list-item {
  min-height: 40px;
}
.mdui-list-dense .mdui-list-item::after {
  height: 40px;
}
.mdui-list-dense .mdui-list-item-icon {
  width: 20px;
  height: 20px;
  font-size: 20px;
}
.mdui-list-dense .mdui-list-item-avatar {
  width: 36px;
  min-width: 36px;
  height: 36px;
  min-height: 36px;
}
.mdui-list-dense .mdui-list-item-content {
  padding-top: 11px;
  padding-bottom: 11px;
  font-size: 13px;
  line-height: 18px;
}
.mdui-list-dense .mdui-list-item-text {
  font-size: 13px;
}
.mdui-list-dense .mdui-list-item-title ~ .mdui-list-item-text {
  margin-top: 2px;
}
.mdui-list-dense .mdui-list-item-one-line {
  height: 18px;
}
.mdui-list-dense .mdui-list-item-two-line {
  height: 36px;
}
.mdui-list-dense .mdui-list-item-three-line {
  height: 54px;
}
/**
 * =============================================================================
 * ************   List dark   ************
 * =============================================================================
 */
.mdui-theme-layout-dark .mdui-list .mdui-subheader::before,
.mdui-theme-layout-dark .mdui-list .mdui-subheader-inset::before {
  background-color: rgba(255, 255, 255, 0.12);
}
.mdui-theme-layout-dark .mdui-list .mdui-subheader:first-child::before,
.mdui-theme-layout-dark .mdui-list .mdui-subheader-inset:first-child::before {
  background-color: transparent;
}
.mdui-theme-layout-dark .mdui-list-item {
  color: #ffffff;
}
.mdui-theme-layout-dark .mdui-list-item:hover {
  background-color: rgba(255, 255, 255, 0.08);
}
.mdui-theme-layout-dark .mdui-list-item-icon {
  color: #ffffff;
}
.mdui-theme-layout-dark .mdui-list-item-text {
  opacity: 0.7;
}
.mdui-theme-layout-dark .mdui-list-item-active {
  background-color: rgba(255, 255, 255, 0.08);
}
/**
 * ==============================================================================
 * ************   List inner theme   ************
 * ==============================================================================
 */
[class*="mdui-color-"] .mdui-list-item {
  color: inherit;
}
[class*="mdui-color-"] .mdui-list-item-icon {
  color: inherit;
}
/**
 * =============================================================================
 * ************   Drawer 抽屉栏导航   ************
 * =============================================================================
 */
/* DOM 加载完后再添加 transition */
body.mdui-loaded {
  transition: padding 0.3s cubic-bezier(0, 0, 0.2, 1);
}
body.mdui-loaded .mdui-drawer {
  transition: all 0.3s cubic-bezier(0, 0, 0.2, 1);
}
/* 抽屉栏外层，默认出现在左侧且隐藏 */
.mdui-drawer {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 5000;
  box-sizing: border-box;
  /* stylelint-disable-next-line */
  width: calc(100% - 56px);
  max-width: 280px;
  margin: 0;
  overflow-x: hidden;
  overflow-y: auto;
  white-space: nowrap;
  will-change: transform;
  -webkit-overflow-scrolling: touch;
}
@media (max-width: 1023px) {
  .mdui-drawer:not(.mdui-drawer-open) {
    box-shadow: none !important;
  }
}
@media (min-width: 600px) {
  .mdui-drawer {
    /* stylelint-disable-next-line */
    width: calc(100% - 64px);
    max-width: 320px;
  }
}
@media (min-width: 1024px) {
  .mdui-drawer {
    width: 240px;
    max-width: none;
  }
  .mdui-drawer.mdui-drawer-close {
    box-shadow: none !important;
  }
}
/* 出现在右侧的抽屉栏 */
.mdui-drawer-right {
  right: 0;
  left: auto;
}
/* 平板上的样式 */
@media (max-width: 1023px) {
  .mdui-drawer {
    /* 始终有背景和阴影 */
    background-color: #fff;
    /* 默认隐藏 */
    transform: translateX(-330px);
    box-shadow: 0 8px 10px -5px rgba(0, 0, 0, 0.2), 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12);
  }
  .mdui-drawer-right {
    /* 右侧 drawer 也默认隐藏 */
    transform: translateX(330px);
  }
}
/* 手机上的样式 */
@media (max-width: 599px) {
  .mdui-drawer {
    transform: translateX(-290px);
  }
  .mdui-drawer-right {
    transform: translateX(290px);
  }
}
/* 强制隐藏抽屉栏 */
.mdui-drawer-close {
  transform: translateX(-290px);
}
.mdui-drawer-close.mdui-drawer-right {
  transform: translateX(290px);
}
@media (min-width: 600px) {
  .mdui-drawer-close {
    transform: translateX(-330px);
  }
  .mdui-drawer-close.mdui-drawer-right {
    transform: translateX(330px);
  }
}
@media (min-width: 1024px) {
  .mdui-drawer-close {
    transform: translateX(-250px);
  }
  .mdui-drawer-close.mdui-drawer-right {
    transform: translateX(250px);
  }
}
/* 强制显示抽屉栏 */
.mdui-drawer-open {
  transform: translateX(0) !important;
}
/* PC 上的样式 */
@media (min-width: 1024px) {
  /* 使该类所在元素获得 padding-left 或 padding-right，避免被抽屉栏覆盖住页面 */
  .mdui-drawer-body-left {
    padding-left: 240px;
  }
  .mdui-drawer-body-right {
    padding-right: 240px;
  }
  /* PC 上默认有上边距 */
  .mdui-appbar-with-toolbar .mdui-drawer {
    top: 64px;
